.reclaim-your-life3 {
  margin: 0;
}
.reclaim-your-life-container4 {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.04em;
  line-height: 150%;
  font-weight: 300;
}
.frame-parent11,
.frame-parent12,
.frame-parent13,
.video1 {
  align-self: stretch;
  display: flex;
  align-items: center;
  gap: var(--gap-45xl);
}
.frame-parent12,
.frame-parent13 {
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-45xl);
}
.frame-parent11,
.video1 {
  flex-direction: column;
  justify-content: flex-start;
  font-size: var(--font-size-base);
}
.video1 {
  background: radial-gradient(50% 50%at 50% 50%, #fff, #fdfbf9);
  padding: var(--padding-69xl) 0;
  gap: var(--gap-69xl);
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-gray-100);
  font-family: var(--font-inter);
}
@media screen and (max-width: 420px) {
  .frame-parent12,
  .frame-parent13 {
    flex-direction: column;
    gap: var(--gap-base);
    align-items: center;
    justify-content: center;
  }
}
