.vector-icon3,
.vector-icon4 {
  width: 7.1px;
  position: relative;
  height: 6.6px;
  object-fit: cover;
}
.vector-icon4 {
  width: 14px;
  height: 6px;
}
.endless-scrolling {
  width: 16px;
  height: 28px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-8xs-4);
}
.endless-scrolling-wrapper {
  width: 48px;
  border-radius: var(--br-81xl);
  background: linear-gradient(rgba(52, 74, 81, 0.26), rgba(52, 74, 81, 0.26)),
    #fff;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs);
  box-sizing: border-box;
}
.reclaim-your-life2 {
  margin: 0;
}
.reclaim-your-life-container3 {
  position: relative;
  letter-spacing: 0.04em;
  line-height: 150%;
  font-weight: 300;
}
.frame-parent10 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-base);
  gap: var(--gap-5xl);
  text-align: center;
  font-size: var(--font-size-base);
  color: var(--color-gray-100);
  font-family: var(--font-inter);
}
