.faqs {
  align-self: stretch;
  position: relative;
  line-height: 44.54px;
  font-weight: 500;
}
.to {
  color: var(--color-slategray);
}
.your {
  color: var(--color-gray-500);
}
.span4 {
  color: var(--color-seagreen);
}
.our-answers-to-your {
  margin: 0;
}
.our-answers-to-container {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-21xl);
  line-height: 55.51px;
  color: var(--color-gray-500);
}
.faqs-parent {
  flex: 1;
  height: 522px;
  flex-direction: column;
  gap: var(--gap-xs);
}
.faq-1-parent,
.faq2,
.faqs-parent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.faq-1-parent {
  flex: 1;
  flex-direction: column;
  font-size: var(--font-size-xl);
  color: var(--color-gray-500);
  font-family: var(--font-inter);
}
.faq2 {
  align-self: stretch;
  background: radial-gradient(50% 50%at 50% 50%, #fff, #e2e2e2);
  flex-direction: row;
  flex-wrap: wrap;
  padding: var(--padding-69xl);
  gap: var(--gap-21xl);
  text-align: left;
  font-size: var(--font-size-29xl-1);
  color: var(--color-slategray);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 960px) {
  .faq2 {
    flex-direction: row;
    gap: var(--gap-xl);
    align-items: flex-start;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 420px) {
  .faq-1-parent,
  .faqs-parent {
    flex: unset;
    align-self: stretch;
  }
  .faq2 {
    flex-direction: column;
    gap: var(--gap-21xl);
    align-items: flex-start;
    justify-content: flex-start;
  }
}
