  /* Colors */
  --white: #fff;
  --color-gray-100: #242323;
  --color-gray-200: #0b1116;
  --color-gray-300: rgba(36, 35, 35, 0.6);
  --color-gray-400: rgba(36, 35, 35, 0.8);
  --color-gray-500: rgba(36, 35, 35, 0.9);
  --color-gray-600: rgba(36, 35, 35, 0.1);
