.how-does-quell1 {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.quell-uses-a1 {
  width: 583px;
  position: relative;
  font-size: var(--font-size-base);
  letter-spacing: 0.02em;
  line-height: 150%;
  height: 48px;
  flex-shrink: 0;
}
.how-does-quell-help-reduce-doo-group {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-5xl);
}
.faq-5-child {
  width: 20px;
  position: relative;
  height: 10px;
}
.faq-5 {
  align-self: stretch;
  border-radius: 0 0 var(--br-5xl) var(--br-5xl);
  background-color: var(--white);
  border-top: 2px solid var(--color-gray-600);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-5xl);
  gap: var(--gap-base);
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-gray-500);
  font-family: var(--font-inter);
}
@media screen and (max-width: 420px) {
  .how-does-quell-help-reduce-doo-group {
    flex: unset;
    align-self: stretch;
  }
  .faq-5 {
    flex-direction: column;
    gap: var(--gap-9xs);
    align-items: flex-start;
    justify-content: flex-start;
  }
}
