.hero-child {
  width: 882px;
  position: absolute;
  margin: 0 !important;
  top: -68px;
  left: -246px;
  height: 878.3px;
  z-index: 0;
}
.control-your-scroll {
  align-self: stretch;
  position: relative;
  background: linear-gradient(180deg, #9ca3a1, #0a1714);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.reclaim {
  font-weight: 600;
}
.reclaim-your-life-container {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-5xl);
  letter-spacing: 0.04em;
  font-family: var(--font-inter);
}
.hero-text {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  z-index: 1;
  font-family: var(--font-poppins);
}
.download-now {
  position: relative;
  letter-spacing: 0.04em;
  font-weight: 600;
}
.mobile-app-store-badge1 {
  width: 189px;
  position: relative;
  border-radius: 8.65px;
  height: 56px;
  overflow: hidden;
  flex-shrink: 0;
  mix-blend-mode: normal;
}
.download-now-button,
.mobile-app-store-badge-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.download-now-button {
  align-self: stretch;
  flex-direction: column;
  align-items: center;
  gap: var(--gap-base);
  z-index: 2;
  font-size: var(--font-size-21xl);
  color: var(--black);
}
.join-the-waitlist {
  flex: 1;
  position: relative;
  letter-spacing: 0.04em;
  line-height: 150%;
}
.join-the-waitlist-and-be-the-f-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}
.enter-your-email {
  flex: 1;
  position: relative;
}
.submit {
  width: 84px;
  position: relative;
  display: inline-block;
  flex-shrink: 0;
}
.button,
.email-field {
  align-self: stretch;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.button {
  background-color: var(--black);
  justify-content: center;
  padding: var(--padding-3xs) var(--padding-13xl);
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-whitesmoke-200);
}
.email-field {
  border-radius: var(--br-81xl);
  background-color: var(--white);
  border: 1px solid var(--black);
  box-sizing: border-box;
  height: 64px;
  flex-shrink: 0;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-9xl);
  gap: var(--gap-3xs);
}
.email-field-wrapper {
  align-items: flex-start;
  padding: 0 var(--padding-45xl);
  text-align: left;
  color: var(--black);
}
.email-field-wrapper,
.hero,
.submit-details {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.submit-details {
  align-items: center;
  gap: var(--gap-13xl);
  z-index: 3;
  font-size: var(--font-size-xl);
}
.hero {
  background: radial-gradient(50% 50%at 50% 50%, #fff, #e2e2e2);
  overflow: hidden;
  align-items: flex-start;
  padding: var(--padding-69xl) 0;
  position: relative;
  gap: var(--gap-69xl);
  text-align: center;
  font-size: var(--font-size-53xl);
  color: var(--color-gray-100);
  font-family: var(--font-inter);
}
@media screen and (max-width: 420px) {
  .enter-your-email {
    flex: unset;
    align-self: stretch;
  }
  .email-field {
    flex-direction: column;
    gap: var(--gap-3xs);
    align-items: center;
    justify-content: flex-start;
  }
}
