.image-4-icon {
  width: 607px;
  position: relative;
  height: 500px;
  object-fit: cover;
}
.video {
  align-self: stretch;
  background-color: var(--color-whitesmoke-100);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-69xl) 0;
}
.cys-is-engineered-container {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.02em;
  line-height: 150%;
}
.three-core-strategies {
  position: relative;
}
.strategies,
.three-core-strategies-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.three-core-strategies-wrapper {
  background-color: var(--color-gray-100);
  overflow: hidden;
  flex-direction: row;
  padding: 4px var(--padding-5xs);
  color: var(--white);
}
.strategies {
  align-self: stretch;
  background-color: var(--color-whitesmoke-100);
  flex-direction: column;
  padding: var(--padding-69xl) 380px;
  gap: var(--gap-5xs);
  font-size: var(--font-size-21xl);
}
.the-scroll {
  color: var(--color-gray-700);
}
.escape-the-scroll-container {
  position: relative;
  line-height: 65px;
}
.begin-your-journey {
  margin: 0;
}
.begin-your-journey-container {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-13xl);
  letter-spacing: 0.02em;
  line-height: 130%;
  font-family: var(--font-inter);
}
.benefit-2,
.escape-the-scroll-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.escape-the-scroll-parent {
  gap: var(--gap-5xl);
}
.benefit-2 {
  background-color: var(--color-whitesmoke-100);
  padding: var(--padding-69xl) var(--padding-base);
  font-family: var(--font-poppins);
}
.cys {
  color: var(--color-seagreen);
}
.mobile-app-store-badge {
  width: 324px;
  position: relative;
  border-radius: 14.84px;
  height: 96px;
  overflow: hidden;
  flex-shrink: 0;
  mix-blend-mode: normal;
}
.mobile-app-store-badge-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame-wrapper {
  width: 324px;
  filter: drop-shadow(0 4px 16px rgba(18, 133, 108, 0.5));
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
}
.download-cys-now-parent {
  align-self: stretch;
  justify-content: flex-start;
  gap: var(--gap-13xl);
}
.cta,
.download-cys-now-parent,
.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cta {
  align-self: stretch;
  height: 394px;
  justify-content: center;
  color: var(--color-gray-200);
}
.homepage {
  position: relative;
  background-color: var(--white);
  width: 100%;
  overflow: hidden;
  justify-content: flex-start;
  text-align: center;
  font-size: var(--font-size-37xl);
  color: var(--color-gray-100);
  font-family: var(--font-inter);
}
