.arcticonsclock {
  width: 64px;
  position: relative;
  height: 64px;
  overflow: hidden;
  flex-shrink: 0;
}
.quietly {
  color: var(--color-gray-700);
}
.works-quietly {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.control-your-scroll2 {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-xl);
  letter-spacing: 0.02em;
  line-height: 150%;
  font-family: var(--font-inter);
}
.works-quietly-parent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-5xl);
}
.arcticonsclock-parent {
  align-self: stretch;
  border-radius: var(--br-13xl);
  background-color: var(--white);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-13xl);
  gap: var(--gap-13xl);
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 960px) {
  .arcticonsclock-parent {
    flex-direction: row;
    gap: var(--gap-base);
    align-items: flex-start;
    justify-content: flex-start;
  }
}
