.ellipse-div {
  width: 319px;
  position: relative;
  border-radius: 50%;
  background: radial-gradient(
    50% 50%at 50% 50%,
    rgba(36, 35, 35, 0.1),
    rgba(245, 249, 248, 0.3)
  );
  height: 41px;
}
.feature-3,
.frame-parent5 {
  display: flex;
  justify-content: flex-start;
}
.frame-parent5 {
  flex: 1;
  flex-direction: column;
  align-items: flex-end;
  gap: var(--gap-3xs);
}
.feature-3 {
  align-self: stretch;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: var(--padding-69xl) var(--padding-212xl);
}
