.b {
  position: relative;
  line-height: 65px;
}
.wrapper {
  width: 64px;
  border-radius: var(--br-45xl);
  border: 3px solid var(--color-gray-100);
  box-sizing: border-box;
  height: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs);
}
.break-the-scroll,
.interrupt-doom-scrolling {
  align-self: stretch;
  position: relative;
  line-height: 70px;
}
.interrupt-doom-scrolling {
  font-size: var(--font-size-13xl);
  letter-spacing: 0.02em;
  line-height: 130%;
  font-family: var(--font-inter);
}
.break-the-scroll-parent,
.frame-parent1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.break-the-scroll-parent {
  align-self: stretch;
  justify-content: flex-start;
  gap: var(--gap-base);
  font-size: var(--font-size-45xl);
}
.frame-parent1 {
  flex: 1;
  height: 692.9px;
  justify-content: center;
  gap: var(--gap-5xl);
}
.icon {
  width: 320px;
  position: relative;
  height: 693.5px;
  object-fit: cover;
}
.frame-container {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-71xl);
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1200px) {
  .frame-container {
    flex-direction: row;
    gap: var(--gap-base);
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 960px) {
  .frame-parent1 {
    flex: unset;
    align-self: stretch;
  }
  .frame-container {
    flex-direction: column;
    gap: var(--gap-base);
    align-items: center;
    justify-content: flex-start;
  }
}
