.b1,
.icon1 {
  position: relative;
}
.icon1 {
  width: 320px;
  height: 647.4px;
  object-fit: cover;
}
.b1 {
  line-height: 65px;
}
.container {
  width: 64px;
  border-radius: var(--br-45xl);
  border: 3px solid var(--color-gray-100);
  box-sizing: border-box;
  height: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs);
}
.mindful-engagement,
.scientifically-backed-breathin {
  align-self: stretch;
  position: relative;
  line-height: 70px;
}
.scientifically-backed-breathin {
  font-size: var(--font-size-13xl);
  letter-spacing: 0.02em;
  line-height: 130%;
  font-family: var(--font-inter);
}
.frame-parent4,
.mindful-engagement-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.mindful-engagement-parent {
  align-self: stretch;
  justify-content: flex-start;
  gap: var(--gap-base);
  font-size: var(--font-size-45xl);
}
.frame-parent4 {
  flex: 1;
  height: 695.5px;
  justify-content: center;
  gap: var(--gap-5xl);
}
.parent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-71xl);
}
.frame-inner {
  width: 319px;
  position: relative;
  border-radius: 50%;
  background: radial-gradient(
    50% 50%at 50% 50%,
    rgba(36, 35, 35, 0.1),
    rgba(245, 249, 248, 0.3)
  );
  height: 41px;
}
.feature-2,
.frame-parent3 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame-parent3 {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-3xs);
}
.feature-2 {
  align-self: stretch;
  background-color: var(--color-whitesmoke-100);
  flex-direction: row;
  flex-wrap: wrap;
  padding: var(--padding-69xl) var(--padding-212xl);
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1200px) {
  .frame-parent4 {
    flex: unset;
    align-self: stretch;
  }
  .parent {
    flex-direction: column;
    gap: var(--gap-base);
    align-items: center;
    justify-content: flex-start;
  }
}
