.frame-item {
  width: 319px;
  position: relative;
  border-radius: 50%;
  background: radial-gradient(
    50% 50%at 50% 50%,
    rgba(36, 35, 35, 0.1),
    rgba(245, 249, 248, 0.3)
  );
  height: 41px;
}
.feature-1,
.frame-parent2 {
  display: flex;
  justify-content: flex-start;
}
.frame-parent2 {
  flex: 1;
  flex-direction: column;
  align-items: flex-end;
  gap: var(--gap-3xs);
}
.feature-1 {
  align-self: stretch;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: var(--padding-69xl) var(--padding-212xl);
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
