.control-your-scroll1 {
  color: var(--color-gray-200);
}
.puts-you-back {
  color: var(--color-gray-700);
}
.control-your-scroll-container {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.04em;
  font-weight: 600;
}
.span {
  font-weight: 600;
}
.our-approach-goes-container {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.02em;
  line-height: 160%;
}
.our-approach-goes-beyond-class-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: var(--font-size-xl);
  font-family: var(--font-inter);
}
.mobile-app-store-badge2 {
  width: 216px;
  position: relative;
  border-radius: 9.89px;
  height: 64px;
  overflow: hidden;
  flex-shrink: 0;
  mix-blend-mode: normal;
}
.mobile-app-store-badge-frame {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.control-your-scroll-puts-you-b-parent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-21xl);
}
.lost-in-scroll,
.pop-up {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.lost-in-scroll {
  flex: 1;
  border-radius: var(--br-53xl);
  background-color: var(--white);
  border-top: 1px solid var(--color-gray-400);
  border-right: 5px solid var(--color-gray-400);
  border-bottom: 17px solid var(--color-gray-400);
  border-left: 5px solid var(--color-gray-400);
  align-items: center;
  padding: var(--padding-45xl);
}
.pop-up {
  align-self: stretch;
  background: radial-gradient(50% 50%at 50% 50%, #fff, #e2e2e2);
  align-items: flex-start;
  padding: var(--padding-69xl) var(--padding-221xl);
  text-align: center;
  font-size: var(--font-size-17xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
