.frame-child {
  width: 48px;
  position: relative;
  height: 48px;
}
.cys1 {
  position: relative;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  font-weight: 800;
  background: linear-gradient(180deg, #9ca3a1, #0a1714);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.vector-parent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
}
.how-it-works1 {
  flex: 1;
  position: relative;
}
.items1,
.navbar1 {
  display: flex;
  flex-direction: row;
}
.items1 {
  flex: 1;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-end;
  gap: var(--gap-base);
  font-size: var(--font-size-xl);
  font-family: var(--font-inter);
}
.navbar1 {
  align-self: stretch;
  background-color: var(--white);
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-45xl);
  gap: var(--gap-581xl);
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-lato);
}
@media screen and (max-width: 1200px) {
  .navbar1 {
    flex-direction: row;
    gap: var(--gap-281xl);
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 960px) {
  .navbar1 {
    flex-direction: row;
    gap: var(--gap-45xl);
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 420px) {
  .how-it-works1 {
    flex: unset;
    align-self: stretch;
  }
  .items1,
  .navbar1 {
    flex-direction: column;
  }
  .items1 {
    gap: var(--gap-base);
    align-items: flex-start;
    justify-content: flex-end;
    flex: unset;
    align-self: stretch;
  }
  .navbar1 {
    gap: var(--gap-9xs);
    align-items: center;
    justify-content: flex-start;
  }
}
