.objects-icon {
  width: 132.4px;
  position: relative;
  height: 56px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.how-it-works {
  flex: 1;
  position: relative;
}
.items,
.navbar {
  display: flex;
  flex-direction: row;
}
.items {
  margin: 0;
  flex: 1;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-end;
  gap: var(--gap-base);
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-inter);
}
.navbar {
  align-self: stretch;
  background-color: var(--white);
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-45xl);
  gap: var(--gap-581xl);
}
.get-early-access,
.your-life-from {
  align-self: stretch;
  position: relative;
}
.get-early-access {
  font-weight: 600;
  background: linear-gradient(180deg, #0b1116, #acc3ca);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.your-life-from {
  font-size: var(--font-size-xl);
  letter-spacing: 0.04em;
  line-height: 150%;
  font-weight: 300;
  font-family: var(--font-inter);
}
.get-early-access-parent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.frame-parent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-45xl);
}
.get-early-access-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.benefits,
.frame-group,
.quell-web {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.frame-group {
  align-self: stretch;
  flex-direction: row;
  flex-wrap: wrap;
  gap: var(--gap-45xl);
}
.benefits,
.quell-web {
  flex-direction: column;
}
.benefits {
  align-self: stretch;
  background: radial-gradient(50% 50%at 50% 50%, #fff, #fdfbf9);
  padding: var(--padding-45xl) var(--padding-281xl);
  gap: var(--gap-45xl);
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
.quell-web {
  position: relative;
  background-color: var(--white);
  width: 100%;
  overflow: hidden;
}
@media screen and (max-width: 1200px) {
  .navbar {
    flex-direction: row;
    gap: var(--gap-281xl);
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 960px) {
  .navbar {
    flex-direction: row;
    gap: var(--gap-45xl);
    align-items: center;
    justify-content: flex-start;
  }
  .get-early-access-parent {
    flex: unset;
    align-self: stretch;
  }
  .frame-parent {
    flex-direction: column;
    gap: var(--gap-base);
    align-items: center;
    justify-content: flex-start;
  }
  .get-early-access-container {
    flex: unset;
    align-self: stretch;
  }
  .frame-group {
    flex-direction: column;
    gap: var(--gap-base);
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 420px) {
  .how-it-works {
    flex: unset;
    align-self: stretch;
  }
  .items,
  .navbar {
    flex-direction: column;
  }
  .items {
    gap: var(--gap-base);
    align-items: flex-start;
    justify-content: flex-end;
    flex: unset;
    align-self: stretch;
  }
  .navbar {
    gap: var(--gap-9xs);
    align-items: center;
    justify-content: flex-start;
  }
  .get-early-access-parent {
    flex: unset;
    align-self: stretch;
  }
  .frame-parent {
    flex-direction: column;
    gap: var(--gap-5xs);
    align-items: center;
    justify-content: flex-start;
  }
}
