.span1 {
  font-weight: 500;
}
.from-users-to-container {
  align-self: stretch;
  position: relative;
  letter-spacing: 0.02em;
  line-height: 150%;
}
.the-cys-story {
  position: relative;
}
.the-cys-story-wrapper {
  background-color: var(--color-gray-100);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-5xs) var(--padding-13xl);
  font-size: var(--font-size-45xl);
  color: var(--white);
}
.from-users-to-innovators-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.were-not-just {
  flex: 1;
  position: relative;
  letter-spacing: 0.02em;
  line-height: 150%;
}
.para-1 {
  flex: 1;
  border-radius: var(--br-13xl);
  background-color: var(--white);
  border-top: 1px solid var(--color-gray-400);
  border-right: 3px solid var(--color-gray-400);
  border-bottom: 12px solid var(--color-gray-400);
  border-left: 3px solid var(--color-gray-400);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-5xl);
}
.our-story2,
.para-1-parent {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
}
.para-1-parent {
  height: 168px;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  gap: var(--gap-29xl);
  font-size: var(--font-size-xl);
}
.our-story2 {
  background-color: var(--color-whitesmoke-100);
  flex-direction: column;
  align-items: center;
  padding: var(--padding-69xl) var(--padding-101xl);
  gap: var(--gap-45xl);
  text-align: center;
  font-size: var(--font-size-21xl);
  color: var(--color-gray-100);
  font-family: var(--font-inter);
}
@media screen and (max-width: 1200px) {
  .para-1-parent {
    flex-direction: row;
    gap: var(--gap-5xl);
    align-items: flex-start;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 960px) {
  .para-1-parent {
    flex-direction: row;
    gap: var(--gap-base);
    align-items: flex-start;
    justify-content: flex-start;
  }
}
@media screen and (max-width: 420px) {
  .para-1-parent {
    flex-direction: row;
    gap: var(--gap-base);
    align-items: flex-start;
    justify-content: flex-start;
  }
}
