.benefits1 {
  background: radial-gradient(50% 50%at 50% 50%, #fff, #e2e2e2);
  padding: var(--padding-69xl) var(--padding-45xl);
  gap: var(--gap-21xl);
  text-align: center;
  font-size: var(--font-size-45xl);
  color: var(--color-gray-100);
  font-family: var(--font-inter);
