.get-early {
  margin: 0;
}
.get-early-access-container1 {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 78px;
  font-weight: 600;
  font-family: inherit;
  background: linear-gradient(180deg, #0b1116, #acc3ca);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.for-ios-and {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-xl);
  letter-spacing: 0.04em;
  line-height: 150%;
  font-weight: 300;
  font-family: var(--font-inter);
}
.get-early-access-parent1 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.icbaseline-apple-icon1 {
  width: 24px;
  position: relative;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.app-store1 {
  position: relative;
}
.button3 {
  border-radius: var(--br-81xl);
  background-color: var(--black);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-base) var(--padding-13xl);
  gap: var(--gap-3xs);
}
.image-14-icon1 {
  width: 24px;
  position: relative;
  height: 24px;
  object-fit: cover;
}
.button-parent,
.download-now-button2 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.button-parent {
  flex-direction: row;
  gap: var(--gap-5xl);
}
.download-now-button2 {
  align-self: stretch;
  flex-direction: column;
  font-size: var(--font-size-xl);
  color: var(--white);
  font-family: var(--font-inter);
}
.vector-icon6 {
  width: 98.4px;
  position: relative;
  height: 116.7px;
  object-fit: cover;
}
.vector-icon7,
.vector-icon8 {
  width: 69.8px;
  position: relative;
  height: 120px;
  object-fit: contain;
}
.vector-icon8 {
  width: 98.4px;
  height: 116.7px;
}
.cta1,
.vector-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
.cta1 {
  align-self: stretch;
  background: radial-gradient(50% 50%at 50% 50%, #fff, #fdfbf9);
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--padding-69xl) 0;
  gap: var(--gap-29xl);
  text-align: center;
  font-size: var(--font-size-53xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
