.vector-icon10,
.vector-icon11,
.vector-icon9 {
  width: 43.6px;
  position: relative;
  height: 39.4px;
}
.vector-icon10,
.vector-icon11 {
  width: 41.7px;
}
.vector-icon11 {
  width: 52px;
}
.vector-parent1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.contact-us1 {
  flex: 1;
  position: relative;
}
.contact-us-group {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-13xl);
}
.footer1,
.frame-parent14 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.frame-parent14 {
  align-self: stretch;
  justify-content: flex-start;
  gap: var(--gap-29xl);
}
.footer1 {
  width: 1440px;
  background: radial-gradient(50% 50%at 50% 50%, #fff, #fdfbf9), #242323;
  overflow: hidden;
  justify-content: center;
  padding: var(--padding-69xl) var(--padding-301xl);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--black);
  font-family: var(--font-inter);
}
@media screen and (max-width: 960px) {
  .footer1 {
    gap: var(--gap-13xl);
    padding: var(--padding-25xl) var(--padding-13xl);
    box-sizing: border-box;
    opacity: 0;
  }
  .footer1.animate {
    animation: 1s ease 0s 1 normal forwards slide-in-top;
  }
  @keyframes slide-in-top {
    0% {
      transform: translateY(-200px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
@media screen and (max-width: 420px) {
  .contact-us-group,
  .vector-parent1 {
    flex-direction: row;
    gap: var(--gap-base);
    align-items: flex-start;
    justify-content: center;
  }
  .contact-us-group {
    gap: var(--gap-9xs);
  }
  .footer1 {
    gap: var(--gap-base);
    padding: var(--padding-xs) var(--padding-base);
    box-sizing: border-box;
    opacity: 0;
  }
  .footer1.animate {
    animation: 1s ease 0s 1 normal forwards slide-in-top;
  }
  @keyframes slide-in-top {
    0% {
      transform: translateY(-200px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
