.control-your-scroll3 {
  align-self: stretch;
  position: relative;
  background: linear-gradient(180deg, #fff, #777);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.vector-icon,
.vector-icon1,
.vector-icon2 {
  width: 43.6px;
  position: relative;
  height: 39.4px;
}
.vector-icon1,
.vector-icon2 {
  width: 41.7px;
}
.vector-icon2 {
  width: 52px;
}
.vector-group {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.contact-us {
  flex: 1;
  position: relative;
}
.contact-us-parent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-13xl);
}
.footer,
.frame-parent7 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.frame-parent7 {
  align-self: stretch;
  justify-content: flex-start;
  gap: var(--gap-29xl);
  font-size: var(--font-size-xl);
  font-family: var(--font-inter);
}
.footer {
  width: 1440px;
  background-color: var(--color-gray-100);
  overflow: hidden;
  justify-content: center;
  padding: var(--padding-69xl) var(--padding-301xl);
  box-sizing: border-box;
  gap: var(--gap-45xl);
  text-align: center;
  font-size: var(--font-size-29xl);
  color: var(--white);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 420px) {
  .contact-us-parent,
  .vector-group {
    flex-direction: row;
    gap: var(--gap-base);
    align-items: flex-start;
    justify-content: center;
  }
  .contact-us-parent {
    gap: var(--gap-9xs);
  }
}
