.hero-item {
  width: 1678px;
  position: absolute;
  margin: 0 !important;
  top: 2165px;
  left: calc(50% - 839px);
  border-radius: 50%;
  background-color: var(--color-darkslategray-100);
  height: 1490px;
  z-index: 0;
}
.control-your {
  margin: 0;
}
.control-your-scroll-container1 {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 103px;
  font-weight: 600;
  font-family: inherit;
  background: linear-gradient(180deg, #0b1116, #acc3ca);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.reclaim-your-life-container1 {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-xl);
  letter-spacing: 0.04em;
  line-height: 150%;
  font-weight: 300;
  font-family: var(--font-inter);
}
.hero-text1 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.icbaseline-apple-icon {
  width: 24px;
  position: relative;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.app-store {
  position: relative;
}
.button1 {
  border-radius: var(--br-81xl);
  background-color: var(--black);
  height: 56px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-base) var(--padding-13xl);
  box-sizing: border-box;
  gap: var(--gap-3xs);
}
.image-14-icon {
  width: 24px;
  position: relative;
  height: 24px;
  object-fit: cover;
}
.download-now-button1,
.hero-text-parent {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
}
.download-now-button1 {
  flex-direction: row;
  justify-content: center;
  gap: var(--gap-base);
  font-size: var(--font-size-xl);
  color: var(--white);
  font-family: var(--font-inter);
}
.hero-text-parent {
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-29xl);
  z-index: 1;
}
.frame-child1,
.frame-icon {
  width: 471.5px;
  position: relative;
  height: 486.4px;
  object-fit: contain;
}
.frame-child1 {
  margin-left: -230px;
}
.frame-parent8,
.hero1 {
  align-self: stretch;
  display: flex;
}
.frame-parent8 {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.hero1 {
  background: radial-gradient(50% 50%at 50% 50%, #fff, #fdfbf9);
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-69xl) 0 0;
  position: relative;
  gap: var(--gap-69xl);
  text-align: center;
  font-size: var(--font-size-83xl);
  color: var(--color-gray-100);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 960px) {
  .frame-parent8 {
    flex-direction: column;
    gap: var(--gap-45xl);
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 420px) {
  .download-now-button1,
  .frame-parent8 {
    flex-direction: column;
    gap: var(--gap-5xs);
    align-items: flex-start;
    justify-content: center;
  }
  .frame-parent8 {
    gap: var(--gap-243xs);
    align-items: center;
  }
}
